<template>
    <div>
    <!-- Cabecera de Estados y Fecha Inicio y Fin -->
		<div class="vx-row" style="padding-bottom:25px">
			<!-- Cabecera de Estado -->
			<div class="vx-col w-full">
				<span class="inline-flex relative">
					<div style="padding-right:10px">
						<vx-card class="">
							<p><strong>ID:</strong> {{datos.id}}</p>
						</vx-card>
					</div>
					<div style="padding-right:10px">
						<vx-card class="">
							<p><strong>Campaña:</strong> {{datos.nombre}}</p>
						</vx-card>
					</div>
                    <div style="padding-right:10px">
						<vx-card class="">
							<p><strong>Inicio:</strong> {{datos.fechaInicio}}</p>
						</vx-card>
					</div>
                    <div style="padding-right:10px">
						<vx-card class="">
							<p><strong>Final:</strong> {{datos.fechaFin}}</p>
						</vx-card>
					</div>
                    <div style="padding-right:10px">
						<vx-card class="">
							<p><strong>Duración:</strong> {{datos.duracion}}</p>
						</vx-card>
					</div>
                    <div style="padding-right:10px">
						<vx-card class="">
							<span v-for="item in datos.medios" :key="item.tipoMedio" class="inline-flex relative">
								<p style="padding-right:3px; padding-left:5px">{{ item.intensidad }}</p>
								<IconifyIcon v-if="item.tipoMedio == 1" :icon="iconos.CelularIcon" width="20" />
								<IconifyIcon v-else-if="item.tipoMedio == 2" :icon="iconos.TelefonoIcon" width="20" />
								<IconifyIcon v-else-if="item.tipoMedio == 3" :icon="iconos.EmailIcon" width="20" />
								<IconifyIcon v-else-if="item.tipoMedio == 4" :icon="iconos.SMSIcon" width="20" />
								<IconifyIcon v-else-if="item.tipoMedio == 5" :icon="iconos.WhatsappIcon" width="20" />
							</span>
						</vx-card>
					</div>
                    <div style="padding-right:10px">
						<vx-card class="">
							<p v-if="datos.estado == 1"><strong>Activo</strong></p>
                            <p v-else-if="datos.estado == 2"><strong>Programado</strong></p>
                            <p v-else-if="datos.estado == 3"><strong>Finalizado</strong></p>
						</vx-card>
					</div>
				</span>
			</div>
		</div>
		<!-- Tabla de Listado de Campañas -->
		<div class="vx-row">
			<div class="vx-col w-full">
                <estadisticas-tabla-campana :datos="datos"></estadisticas-tabla-campana>
			</div>
		</div>
    </div>
</template>

<script>
import IconifyIcon from '@iconify/vue';
import CelularIcon from '@iconify/icons-ic/baseline-local-phone';
import TelefonoIcon from '@iconify/icons-entypo/old-phone';
import EmailIcon from '@iconify/icons-ic/round-email';
import SMSIcon from '@iconify/icons-ic/baseline-sms';
import WhatsappIcon from '@iconify/icons-dashicons/whatsapp';
import flatPickr from 'vue-flatpickr-component';
import 'flatpickr/dist/flatpickr.css';
import moment from 'moment';
import EstadisticasTablaCampana from './EstadisticasTablaCampana.vue';
import datos from './usuarias.json';
import axios from 'axios';

export default {
    name: 'Estadistica',
    components: {
        IconifyIcon,
        flatPickr,
        EstadisticasTablaCampana
    },
    created () {
        this.idCampana = this.$route.params.idCampana;
        this.cargarEstadisticas(this.$route.params.idCampana);
    },
    data () {
        return {
            datos,
            idCampana: '',
			iconos: {
				CelularIcon,
				TelefonoIcon,
				EmailIcon,
				SMSIcon,
				WhatsappIcon,
            },
            wasSidebarOpen: null,
        }
    },
    methods: {
        cargarEstadisticas(idCampana){
			let data = { id: idCampana };
            axios.post(this.url.estadisticas, data).then((res)=>{
				this.datos = res.data;
			}).catch((error) =>{
				this.$vs.notify({
					color:'danger',
					title:'Error al cargar las Estadíticas',
					text: error,
					iconPack: 'feather', icon:'icon-alert-circle'
				});
				console.log(error);
			});
        }
    },
    mounted () {
        this.wasSidebarOpen = this.$store.state.reduceButton
        this.$store.commit('TOGGLE_REDUCE_BUTTON', true)
    },
    beforeDestroy () {
        if (!this.wasSidebarOpen) this.$store.commit('TOGGLE_REDUCE_BUTTON', false)
	},
	computed:{
		url(){
			return this.$store.state.urlsAPI;
		},
		infoUser(){
			return this.$store.state.loginInfo;
		}
	},
}
</script>