<template>
	<div id="ag-grid-demo">
		<vx-card>

			<!-- TABLE ACTION ROW -->
			<div class="flex flex-wrap justify-between items-center">

				<!-- ITEMS PER PAGE -->
				<div class="mb-4 md:mb-0 mr-4 ag-grid-table-actions-left">
					<vs-dropdown vs-trigger-click class="cursor-pointer">
						<div class="p-4 border border-solid d-theme-border-grey-light rounded-full d-theme-dark-bg cursor-pointer flex items-center justify-between font-medium">
							<span class="mr-2">{{ currentPage * paginationPageSize - (paginationPageSize - 1) }} - {{ datos.estadistica.length - currentPage * paginationPageSize > 0 ? currentPage * paginationPageSize : datos.estadistica.length }} de {{ datos.estadistica.length }}</span>
							<feather-icon icon="ChevronDownIcon" svgClasses="h-4 w-4" />
						</div>
						<vs-dropdown-menu>
							<vs-dropdown-item @click="gridApi.paginationSetPageSize(5)">
								<span>5</span>
							</vs-dropdown-item>
							<vs-dropdown-item @click="gridApi.paginationSetPageSize(20)">
								<span>20</span>
							</vs-dropdown-item>
							<vs-dropdown-item @click="gridApi.paginationSetPageSize(50)">
								<span>50</span>
							</vs-dropdown-item>
							<vs-dropdown-item @click="gridApi.paginationSetPageSize(100)">
								<span>100</span>
							</vs-dropdown-item>
							<vs-dropdown-item @click="gridApi.paginationSetPageSize(150)">
								<span>150</span>
							</vs-dropdown-item>
						</vs-dropdown-menu>
					</vs-dropdown>
					<vs-button v-if="infoUsuarias" @click="infoUsuarias=!infoUsuarias" class="mb-4 md:mb-0 ml-5" >Ver Información de las Usuarias</vs-button>
					<vs-button v-else @click="infoUsuarias=!infoUsuarias" class="mb-4 md:mb-0 ml-5" >Ver Información de la Campaña</vs-button>
					<vs-button @click="modalInfoMensajes=true" class="mb-4 md:mb-0 ml-5" >Info Mensajes</vs-button>
				</div>

				<!-- TABLE ACTION COL-2: SEARCH & EXPORT AS CSV -->
				<div class="flex flex-wrap items-center justify-between ag-grid-table-actions-right">
					<vs-input class="mb-4 md:mb-0 mr-4" v-model="searchQuery" @input="updateSearchQuery" placeholder="Buscar..." />
					<vs-button class="mb-4 md:mb-0" @click="gridApi.exportDataAsCsv()">Exportar CSV de Usuarias</vs-button>
				</div>
			</div>
			<ag-grid-vue v-if="infoUsuarias"
				ref="agGridTable"
				:gridOptions="gridOptions"
				class="ag-theme-material w-100 my-4 ag-grid-table"
				:columnDefs="columnCampanias"
				:defaultColDef="defaultColDef"
				:rowData="datos.estadistica"
				rowSelection="multiple"
				colResizeDefault="shift"
				:animateRows="true"
				:floatingFilter="true"
				:pagination="true"
				:paginationPageSize="paginationPageSize"
				:suppressPaginationPanel="true"
				:enableRtl="$vs.rtl">
			</ag-grid-vue>
			<ag-grid-vue v-else
				ref="agGridTable"
				:gridOptions="gridOptions"
				class="ag-theme-material w-100 my-4 ag-grid-table"
				:columnDefs="columnUsuarias"
				:defaultColDef="defaultColDef"
				:rowData="datos.estadistica"
				rowSelection="multiple"
				colResizeDefault="shift"
				:animateRows="true"
				:floatingFilter="true"
				:pagination="true"
				:paginationPageSize="paginationPageSize"
				:suppressPaginationPanel="true"
				:enableRtl="$vs.rtl">
			</ag-grid-vue>
			<vs-pagination
				:total="totalPages"
				:max="maxPageNumbers"
				v-model="currentPage" />

				<!-- Modal para Detalles de los mensjaes de los medios -->
				<vs-popup title="Detalles de los Mensajes" :active.sync="modalInfoMensajes">
						<vs-row v-for="item in datos.medios" :key="item.tipoMedio">
							<vs-col v-if="item.tipoMedio==1" vs-type="flex" vs-w="3" class="details"><strong>Celular</strong></vs-col>
							<vs-col v-else-if="item.tipoMedio==2" vs-type="flex" vs-w="3" class="details"><strong>Teléfono Fijo</strong></vs-col>
							<vs-col v-else-if="item.tipoMedio==3" vs-type="flex" vs-w="3" class="details"><strong>Email</strong></vs-col>
							<vs-col v-else-if="item.tipoMedio==4" vs-type="flex" vs-w="3" class="details"><strong>SMS</strong></vs-col>
							<vs-col v-else-if="item.tipoMedio==5" vs-type="flex" vs-w="3" class="details"><strong>WhatsApp</strong></vs-col>
							<vs-col vs-type="flex" vs-w="9" class="details">{{ item.sms }}</vs-col>
						</vs-row>
						<vs-col vs-type="flex" vs-w="8" class="details">
							<vs-button color="success" type="border" @click="modalInfoMensajes=false">Volver atrás</vs-button>
						</vs-col>
				</vs-popup>
		</vx-card>
	</div>
</template>

<script>
import { AgGridVue } from 'ag-grid-vue';
import '@/assets/scss/vuexy/extraComponents/agGridStyleOverride.scss';
import moment from 'moment';

export default {
	props: ['datos'],
	components: {
		AgGridVue,
	},
	data () {
		return {
			modalInfoMensajes: false,
			infoUsuarias: true,
			searchQuery: '',
			gridOptions: {},
			maxPageNumbers: 7,
			gridApi: null,
			defaultColDef: {
				sortable: true,
				editable: false,
				resizable: true,
				suppressMenu: true
			},
			columnUsuarias: [
				{
					headerName: 'Nombre',
					field: 'nombre',
					width: 280,
					filter: true,
				},
				{
					headerName: 'Identidad',
					field: 'identidad',
					filter: true,
					width: 150,
				},
				{
					headerName: 'Fecha de Nacimiento',
					field: 'fecha_nacimiento',
					filter: true,
					width: 130
				},
				{
					headerName: 'Celular',
					field: 'celular',
					filter: true,
					width: 140
				},
				{
					headerName: 'Teléfono',
					field: 'telefono',
					filter: true,
					width: 130
				},
				{
					headerName: 'Email',
					field: 'email',
					filter: true,
					width: 280,
				},
			],
			columnCampanias: [
				{
					headerName: 'Usuarias',
					children: [
						{
							headerName: 'Nombre',
							field: 'nombre',
							width: 280,
							filter: true,
						},
						{
							headerName: 'Identidad',
							field: 'identidad',
							filter: true,
							width: 150,
							columnGroupShow: 'open'
						},
						{
							headerName: 'Fecha de Nacimiento',
							field: 'fecha_nacimiento',
							filter: true,
							width: 130,
							columnGroupShow: 'open'
						},
						{
							headerName: 'Celular',
							field: 'celular',
							filter: true,
							width: 140,
							columnGroupShow: 'open'
						},
						{
							headerName: 'Teléfono',
							field: 'telefono',
							filter: true,
							width: 130,
							columnGroupShow: 'open'
						},
						{
							headerName: 'Email',
							field: 'email',
							filter: true,
							width: 280,
							columnGroupShow: 'open'
						},
					],
				}
			],
		}
	},
	watch: {
		'$store.state.windowWidth' (val) {
			if (val <= 576) {
				this.maxPageNumbers = 4
				this.gridOptions.columnApi.setColumnPinned('email', null)
			} else this.gridOptions.columnApi.setColumnPinned('email', 'left')
		}
	},
	computed: {
		paginationPageSize () {
			if (this.gridApi) return this.gridApi.paginationGetPageSize()
			else return 50
		},
		totalPages () {
			if (this.gridApi) return this.gridApi.paginationGetTotalPages()
			else return 0
		},
		currentPage: {
			get () {
				if (this.gridApi) return this.gridApi.paginationGetCurrentPage() + 1
				else return 1
			},
			set (val) {
				this.gridApi.paginationGoToPage(val - 1)
			}
		}
	},
	methods: {
		updateSearchQuery (val) {
			this.gridApi.setQuickFilter(val)
		},
		inicializar(datos){
			let suma = 0;
			for(let i in this.datos.medios){
				suma += this.datos.medios[i].intensidad;
			}
			suma *= this.datos.duracion;

			let contador=1;
			for(let j=0; j < this.datos.duracion; j++){
				let fecha = moment(this.datos.fechaInicio, 'DD-MM-YYYY').add(j, 'd').format('DD MMMM YYYY');
				let prueba = { headerName: 'Día '+(j+1)+' >> '+ fecha};
				let children = [];
				for(let k in this.datos.medios){          
					for(let l=0; l < this.datos.medios[k].intensidad; l++){
						let tipoMedio = '';
						if(this.datos.medios[k].tipoMedio == 1 ){ tipoMedio='Celular ' }
						else if(this.datos.medios[k].tipoMedio == 2 ){ tipoMedio='Telefono ' }
						else if(this.datos.medios[k].tipoMedio == 3 ){ tipoMedio='Email ' }
						else if(this.datos.medios[k].tipoMedio == 4 ){ tipoMedio='SMS ' }
						else if(this.datos.medios[k].tipoMedio == 1 ){ tipoMedio='WhatsApp ' }
						children.push({
							headerName: tipoMedio+this.datos.medios[k].Horas[l],
							field: 'medio_'+String(contador),
							width: 150,
							filter: true,
							cellStyle: function(params) {
								if (params.value=='Si') {
									return {color: 'rgba(var(--vs-success))', backgroundColor: 'rgba(var(--vs-success),0.15)'};
								}if(params.value=='No'){
									return {color: 'rgba(var(--vs-danger))', backgroundColor: 'rgba(var(--vs-danger),0.15)'};
								}
							}
						});
						contador++;
					}
				}
				prueba.children = children;
				this.columnCampanias.push(prueba);
			}
		}
	},
	mounted () {
		this.gridApi = this.gridOptions.api;
		this.inicializar(this.datos);

		/* =================================================================
			NOTE:
			Header is not aligned properly in RTL version of agGrid table.
			However, we given fix to this issue. If you want more robust solution please contact them at gitHub
		================================================================= */
		if (this.$vs.rtl) {
			const header = this.$refs.agGridTable.$el.querySelector('.ag-header-container')
			header.style.left = `-${  String(Number(header.style.transform.slice(11, -3)) + 9)  }px`
		}
	}
}
</script>


<style lang="scss">
	.details{
		padding: 10px;
	}
</style>